body{
  text-align: center;
  background-color: rgb(0, 0, 0);
  color: white;
  
  
}

.app{
  display: inline-block;
  margin: 0 auto;
  padding: 20px;

  
}


.hidden {
  display: none;
}


img{
  max-width: 100%;

}

.collection_title {

  text-decoration: underline;
  cursor: pointer;
  
}

.collection_title:hover{

  color: greenyellow;
}

video{

  max-width: 300px;
  
}

.nftContainer {

  display: grid;
  justify-content: center;
  grid-template-columns: auto auto;
  gap: 10px;

}

.btnContain{

  display: grid;
  justify-content: space-between;
  grid-template-columns: auto auto auto;
  gap: 10px;

}


.send-button{
  margin-top: 5%;
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 30%;
  height: 2rem;
  background-color: rgb(2, 121, 91);
  border: none;
  font-size: 1rem;
  color: white;
}



.send-button:hover{
  background-color: rgb(26, 175, 138);
  cursor: pointer;
}


.send-input{

  width: 75%;
}






@media only screen and (max-width: 600px) {
 .address {
    font-size: small;
  }
}
